import { ORDER_CREATE_REQUEST, ORDER_CREATE_SUCCESS, ORDER_CREATE_FAIL,ORDER_DETAILS_REQUEST,ORDER_DETAILS_SUCCESS,ORDER_DETAILS_FAIL, ORDER_PAYMENT_REQUEST, ORDER_PAYMENT_FAIL, ORDER_PAYMENT_SUCCESS, ORDER_PAYMENT_RESET, ORDER_ALL_ORDERS_REQUEST, ORDER_ALL_ORDERS_SUCCESS, ORDER_ALL_ORDERS_FAIL, ORDER_VIEW_REQUEST, ORDER_VIEW_SUCCESS, ORDER_VIEW_FAIL, ORDER_DELIVER_REQUEST, ORDER_DELIVER_SUCCESS, ORDER_DELIVER_FAIL, ORDER_DELIVER_RESET} from "../constants/orderConstants"

export const orderCreateReducer = (state = {}, action) => {
    switch(action.type){
        case ORDER_CREATE_REQUEST:
            return {
                loading:true
            }
        case ORDER_CREATE_SUCCESS:
            return{
                loading:false,
                success: true,
                order: action.payload
            }
        case ORDER_CREATE_FAIL:
            return {
                loading: false,
                error: action.payload,
            }
            default:
                return state
    }

}


export const orderDetailsReducer = (state = {loading: true, orderItems: [], deliveryAddress: {} }, action) => {
    switch(action.type){
        case ORDER_DETAILS_REQUEST:
            return {
                ...state,
                loading:true,
            }
        case ORDER_DETAILS_SUCCESS:
            return{
                loading:false,
                order: action.payload
            }
        case ORDER_DETAILS_FAIL:
            return {
                loading: false,
                error: action.payload,
            }
            default:
                return state
    }

}


