import React, {useState,useEffect} from 'react'
import {Link} from 'react-router-dom'
import {Form, Button, Row, Col} from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loading from '../components/Loading'
import {getUserDetails, updateUser} from '../actions/userActions'
import FormContainer from '../components/FormContainer'
import { USER_UPDATE_RESET } from '../constants/userConstants'

const EditUserPage = ({match,history}) => {
    const userId=match.params.id

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [isAdmin, setIsAdmin] = useState(false)
    
    

    const dispatch = useDispatch()

    const userDetails = useSelector((state) => state.userDetails)
    const {loading,error,user} = userDetails

    const userUpdate = useSelector((state) => state.userUpdate)
    const {loading:updateLoading,error:updateError,success:updateSuccess} = userUpdate


   

   useEffect(() => {
    if(updateSuccess) {
        dispatch({type: USER_UPDATE_RESET})
        history.push('/admin/userlist')
    }   else {
        if(!user.name|| user._id !== userId) {
            dispatch(getUserDetails(userId))
        }else {
            setName(user.name)
            setEmail(user.email)
            setIsAdmin(user.isAdmin)
        }
    }

    
  }, [dispatch,userId,user,updateSuccess,history])

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(updateUser({ _id:userId,name,email,isAdmin}))
    
  }
    
    return (
        <>
            <Link to='/admin/userlist' className='btn btn-dark my-3'>
                go Back
            </Link>
            <FormContainer>
        <h1>Edit a User</h1>
        {updateLoading && <Loading />}
        {updateError && <Message variant='danger'>{updateError}</Message>}
        {loading ? <Loading />: error? <Message variant='danger'>{error}</Message> :(
            <Form onSubmit = {submitHandler}>
            <Form.Group controlId='name'>
                <Form.Label>Name</Form.Label>
                <Form.Control type='name' placeholder='Enter name' value={name}
                onChange={(e) =>setName(e.target.value)}>

                </Form.Control>
            </Form.Group>
            <Form.Group controlId='email'>
                <Form.Label>Email Address</Form.Label>
                <Form.Control type='email' placeholder='Enter email' value={email}
                onChange={(e) =>setEmail(e.target.value)}>

                </Form.Control>
            </Form.Group>

            <Form.Group controlId='isAdmin'>
                
                <Form.Check type='checkbox' label='Is Admin' checked={isAdmin}
                onChange={(e) =>setIsAdmin(e.target.checked)}>
                    
                </Form.Check>
                
            </Form.Group>

            
            <Button className ='mt-3 btn-block btn-dark' type='submit' variant='primary' >
                Update
            </Button>
        </Form>
        )}
        

    </FormContainer>
        </>
    )
 
}
export default EditUserPage