
import React, {useEffect} from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import { Button,Table} from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loading from '../components/Loading'
import { usersList } from '../actions/userActions'
import { deleteUser } from '../actions/userActions'


const ListOfUsersPage = () => {
    const dispatch= useDispatch()
    const userList = useSelector(state => state.userList)
    const {loading,error,users} = userList

    const userDelete = useSelector(state => state.userDelete)
    const {success:successDelete} = userDelete

    useEffect(() => {
        dispatch(usersList())
    }, [dispatch,successDelete])

    const deleteHandler = (id) => {
        dispatch(deleteUser(id))
    }
        

  return (
    <>
    <h1>Users</h1>
    {loading ? <Loading /> : error ? <Message variant='danger'>{error}</Message> : (
        <Table striped bordered hover responsive className='table-sm'>
            <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Is Admin</th>
                    <th>Edit / Delete</th>
                </tr>
            </thead>
            <tbody>
                {users.map(user => (
                    <tr key={user._id}>
                        <td>{user._id}</td>
                        <td>{user.name}</td>
                        <td>{user.email}</td>
                        <td>{user.isAdmin ? (<i className='fas fa-check' style={{color:'green'}}></i>):
                        <i className="fa fa-times" aria-hidden="true" style={{color:'red'}}></i>
                        }
                        </td>
                        <td>
                            <LinkContainer to={`/admin/user/${user._id}/edit`}>
                                <Button variant='dark' className='btn-sm'>
                                    <i className='fas fa-edit'></i>
                                </Button>
                                
                            </LinkContainer>
                            <Button variant='danger' className='btn-sm' onClick={() =>
                            deleteHandler(user._id)}>
                                <i className='fas fa-trash'></i>
                            </Button>
                        </td>
                    </tr>
                ))}
            </tbody>
        </Table>
    )}
    </>
  )
}

export default ListOfUsersPage