
import React, {useEffect} from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import { Button,Table} from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loading from '../components/Loading'
import { usersList } from '../actions/userActions'
import { ViewOrders } from '../actions/orderActions'


const ListOfOrdersPage = () => {
    const dispatch= useDispatch()
    const orderView = useSelector(state => state.orderView)
    const {loading,error,orders} = orderView

    

    useEffect(() => {
        dispatch(ViewOrders())
    }, [dispatch,ViewOrders])

    
        

  return (
    <>
    <h1>Orders</h1>
    {loading ? <Loading /> : error ? <Message variant='danger'>{error}</Message> : (
        <Table striped bordered hover responsive className='table-sm'>
            <thead>
                <tr>
                    <th>ID</th>
                    <th>User</th>
                    <th>Date</th>
                    <th>Total Price</th>
                    <th>Paid</th>
                    <th>Delivered</th>
                    <th>Details</th>
                </tr>
            </thead>
            <tbody>
                {orders.map((order) => (
                    <tr key={order._id}>
                        <td>{order._id}</td>
                        <td>{order.user && order.user.name}</td>
                        <td>{order.createdAt.substring(0,10)}</td>
                        <td>{order.totalPrice}</td>
                        <td>{order.isPaid ? (order.paidAt.substring(0,10)):
                        <i className="fa fa-times" aria-hidden="true" style={{color:'red'}}></i>
                        }
                        </td>
                        <td>{order.isDelivered ? (order.deliveredAt.substring(0,10)):
                        <i className="fa fa-times" aria-hidden="true" style={{color:'red'}}></i>
                        }
                        </td>
                        <td>
                            <LinkContainer to={`/order/${order._id}/edit`}>
                                <Button variant='dark' className='btn-sm'>
                                    <i className='fas fa-edit'></i>
                                </Button>
                                
                            </LinkContainer>
                            
                        </td>
                    </tr>
                ))}
            </tbody>
        </Table>
    )}
    </>
  )
}

export default ListOfOrdersPage